.container {
  width: calc(100% - 40px);
  margin: 0 auto;
  max-width: 1440px;

  @include onTablet {
    width: calc(100% - 54px);
  }

  @include onLaptop {
    width: calc(100% - 200px);
    margin: 0 50px 0 150px;
  }

  @include onDesktop {
    width: calc(100% - 150px);
    margin: 0 auto;
  }
}
